const importAll = (requireContext: __WebpackModuleApi.RequireContext) => {
  requireContext.keys().forEach(requireContext);
}
try{
  importAll(require.context('../assets/iconfont', true, /\.svg$/))
}catch(err){
  console.log(err);
}
export default {
  props: {
    name: {
      type: String
    }
  }
};
