













import Vue from 'vue';
import { Component,Watch,Prop } from 'vue-property-decorator';
@Component
export default class Notes extends Vue {
  @Prop() value!: string;
  @Prop() name!: string;
  @Prop() placeholder!: string;
  @Prop() type!: string;
  // 监听值变化 传回去
  onValueChanged(value: string) {
    this.$emit('update:value', value);
  }
}
